@import '../../sass/index';
@import "react-awesome-lightbox/build/style.css";

.ant-row {

    padding-left: $left; 
    
}

.main-content {

    .inform-msg {

        margin-left: 40%;

        .clickable {

            &:hover {

                color: $color1;
                border-color: $color1;

            }
        }

        .highlighted {

            font-weight: bold;

        }
    }

}

.flatbread {

    text-align: justify;
    // top right bottom left
    padding: 5px 40px 0px $left;

    h1 {
        font-size: 30px;
    }

    h2 {
        font-size: 25px;
    }
    
    td {
        padding-right: 40px;
    }

}

.video-listing {

    &:hover {
        color: $color0;
    }

}